<template>
  <div class="nav">
    <div class="nav-inner">
      <div class="logo">
        <img src="https://static2.cnodejs.org/public/images/cnodejs_light.svg" alt="" />
      </div>
      <div class="search">
        <input type="text" />
      </div>
      <ul class="list">
        <li><router-link to="/">首页</router-link></li>
        <li @click="show">发布</li>
        <li><router-link to="/message/Unreadmessage">消息<span class="Unmessage">{{ getcount }}</span></router-link></li>
        <li><router-link to="/topic_collect/collect">收藏</router-link></li>
        <li v-show="token">
          <router-link to="/user/caobowen1224">
            <p>{{ token.loginname }}</p>
          </router-link>
        </li>

        <li v-show="token"><router-link to="/user/caobowen1224"><img class="img" :src='`${token.avatar_url}`'
              alt=""></router-link></li>
        <li v-if="!token">登录</li>
        <li v-else @click="back">退出</li>
      </ul>
    </div>

  </div>
</template>

<script>


export default {

  data() {
    return {
      token: null,
    };
  },
  methods: {
    async back() {
      sessionStorage.removeItem("userInfo");
      sessionStorage.removeItem("token");
      await this.$router.push("/login");
    },
    show() {
      this.$store.commit('show')
    }
  },
  computed:{
   getcount(){
    return this.$store.state.count
   }
  },
   created() {
    this.token = sessionStorage.getItem("userInfo");
    this.token = JSON.parse(this.token);
    this.$store.dispatch('getCount')
  },

};
</script>

<style>
.nav {
  width: 100%;
  background-color: #444;
  font-size: 13px;
}

.nav-inner {
  width: 1400px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  font-size: 0;
}

.nav-inner .logo {
  width: 120px;
}

.nav-inner .logo img {
  vertical-align: middle;
}

.nav-inner .search input {
  width: 206px;
  padding-left: 24px;
  outline: none;
  line-height: 26px;
  margin-left: 20px;
  border-radius: 20px;
  border: 0;
  transition: all 0.5s;
  background: url("https://static2.cnodejs.org/public/images/search.e53b380a.hashed.png");
  background-repeat: no-repeat;
  background-position: 3px center;
  background-color: #fff;
}

.nav-inner .search input:hover {
  background-color: #fff;
}

.nav-inner ul {
  display: flex;
  margin-left: auto;
}

.nav-inner ul li {
  font-size: 13px;
  color: rgb(204, 204, 204);
  cursor: pointer;
  margin-left: 15px;
  margin-right: 15px;
  line-height: 50px;
}

.nav-inner ul li a {
  color: rgb(204, 204, 204);
  text-decoration: none;
}

.nav-inner ul li a .Unmessage {
  display: inline-block;
  line-height: 15px;
  background-color: greenyellow;
  color: #fff;
  font-size: 12px;
  text-align: center;
  border-radius: 50%;
  vertical-align: super;
  padding: 2px 5px;
}

.nav-inner ul li a:hover {
  color: #fff;
  text-decoration: none;
}

.nav-inner ul li:hover {
  color: #fff;
}

.img {
  width: 20px;
  vertical-align: middle;

}
</style>